import React, { Component } from 'react';
import './item-add.css';

export default class ItemAdd extends Component {
    state = {
        value: ''
      };

    handleChange = (event) => {
        this.setState({
            value: event.target.value
        });
    };

    onSubmit = (e) => {
        e.preventDefault();
        this.props.onAdded(this.state.value);
        this.setState({
            value: ''
        });
    }

    isDisabled(items) {
        return items.length > 5 ? true : false;
    }
    render() {
        const { onAdded, items } = this.props;
        return (
            <form className="item-add-form d-flex"
                  onSubmit={this.onSubmit}>
                <input type="text" 
                       className="form-control" 
                       placeholder="Enter new item"
                       value={this.state.value} 
                       onChange={this.handleChange}
                       disabled={this.isDisabled(items)}/>
                <button 
                className="btn btn-outline-secondary" disabled={this.isDisabled(items)}>
                    Add Item
                </button>
            </form>
        )
    }
}