import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import TodoList from '../todo-list';
import SearchPanel from '../search-panel';
import AppHeader from '../app-header';
import ItemStatusFilter from '../item-status-filter'
import ItemAdd from '../item-add'

import './app.css';

export default class App extends Component {

  maxId = 100;

  state = {
    todoData: [],
    term: '',
    filter: 'all'
  };
  
  createTodoItem(label) {
    return {
      label, 
      important: false, 
      done: false, 
      id: this.maxId++
    }
  }

  deleteItem = (id) => {
    this.setState(({todoData}) => {
      const idx = todoData.findIndex((el) => el.id === id);

      const newArr = [
        ...todoData.slice(0, idx), 
        ...todoData.slice(idx + 1)
      ];

      return {
        todoData: newArr
      };
    });
  };

  addItem = (text) => {
  const newItem = this.createTodoItem(text);
    this.setState(({todoData}) => {
      const newArr = [
        ...todoData, 
        newItem
      ];
      return {
        todoData: newArr
      }
    });
  };

  onToggleImportant = (id) => {
    this.setState(({todoData}) => {
      return {
        todoData: this.toggleProperty(todoData, id, 'important')
      };
    });
  };

  onToggleDone = (id) => {
    this.setState(({todoData}) => {
      return {
        todoData: this.toggleProperty(todoData, id, 'done')
      };
    });
  };

toggleProperty(arr, id, propName) {
  const idx = arr.findIndex((el) => el.id === id);
  const oldItem = arr[idx];
  const newItem = {...oldItem, [propName]: !oldItem[propName]};

    return [
    ...arr.slice(0, idx), 
    newItem,
    ...arr.slice(idx + 1)
  ];

}

filter(items, filter) {
  switch(filter) {
    case 'all': 
      return items;
    case 'active':
      return items.filter(e => !e.done);
    case 'done':
      return items.filter(e => e.done);
    default:
      return items;
  }
}

onFilterChange = (filter) => {
  this.setState({filter});
}

searched = (text) => {
  this.setState(({term}) => {
    return {
      term: text
    };
  });
}
search(term, items) {
  if (term.length === 0) {
    return items;
  }
  return items.filter(e => e.label.indexOf(term) > -1 );
}

  render() {
    
    const { todoData, term, filter } = this.state;
    const visible = this.filter(
      this.search(term, todoData), filter);
    const doneCount = todoData
                      .filter(el => el.done).length;
    const todoCount = todoData.length - doneCount;

    return (
      <div className="todo-app">
        <AppHeader toDo={todoCount} done={doneCount}/>
        <div className="top-panel d-flex">
          <SearchPanel searched={this.searched}/>
          <ItemStatusFilter filtered={filter} onFilterChange={this.onFilterChange}/>
        </div>
        <TodoList 
        todos={visible}
        onDeleted={this.deleteItem}
        onToggleDone={this.onToggleDone}
        onToggleImportant={this.onToggleImportant}
        />
        <ItemAdd 
        onAdded={this.addItem}
        items={todoData}
        />
      </div>
    );
  }
};

